import { takeLatest, call, put, all } from 'redux-saga/effects';

import api from '../../../services/api';
import { loginSucess, signFailure } from './actions';
import history from '../../../services/history';

import { toast } from 'react-toastify';

export function* singIn({ payload }) {
  try {
    const { email, password } = payload;

    const res = yield call(api.post, 'sessions', {
      email,
      password,
    });

    const { token, user } = res.data;

    api.defaults.headers.Authorization = `Bearer ${token}`;

    yield put(loginSucess(token, user));

    history.push('/accounts');
  } catch (error) {
    toast.error('As credenciais de acesso fornecidas s√£o inv√°lidas. Tente novamente!')
    yield put(signFailure());
  }
}

export function setToken({ payload }) {
  if (!payload) return;

  const { token } = payload.auth;
  api.defaults.headers.Authorization = `Bearer ${token}`;
}

export function singOut() {
  history.push('/');
}

export default all([
  takeLatest('persist/REHYDRATE', setToken),
  takeLatest('@auth/SIGN_IN_REQUEST', singIn),
  takeLatest('@auth/SIGN_OUT', singOut),
]);
