import { takeLatest, call, put, all } from 'redux-saga/effects';

import api from '../../../services/api';
import history from '../../../services/history';

import {
  accountsFailure,
  accountsSearchSuccess,
  accountsSaveSuccess,
  accountsDeleteSuccess,
} from './actions';

import { toast } from 'react-toastify';

function* searchAccounts({ payload }) {
  try {
    const { name, page } = payload.data;

    const res = yield call(api.get, 'accounts', {
      params: {
        name: name || '',
        page,
      },
    });

    yield put(accountsSearchSuccess(res.data));
  } catch (error) {
    toast.info('Erro na consulta de usu√°rios!');
    yield put(accountsFailure());
  }
}

function* saveAccount({ payload }) {
  const { id } = payload.data;

  if (id) {
    yield updateAccount(payload.data);
  } else {
    yield addAccount(payload.data);
  }
}

function* addAccount(data) {
  try {
    const res = yield call(api.post, 'accounts', data);
    yield put(accountsSaveSuccess(res.data));

    history.push('/welcome');

  } catch (err) {

    const { error } = err.response.data

    if (error === 'Email already exists') {
      toast.info('O e-mail fornecido j√° foi cadastrado!')
    } else if (error === 'CPF already exists') {
      toast.info('O CPF fornecido j√° foi cadastrado!')
    }
    else {
      toast.error('Erro cadastrar usu√°rio!');
    }

    yield put(accountsFailure());
  }
}

function* updateAccount(data) {
  try {
    const res = yield call(api.put, `accounts/${data.id}`, data);

    toast.warn('Dados atualizados com sucesso.');
    yield put(accountsSaveSuccess(res.data));
  } catch (error) {
    toast.error('Ocorreu um erro na atualiza√ß√£o dos dados!');
    yield put(accountsFailure());
  }
}

function* deleteAccount({ payload }) {
  try {
    const { id } = payload;

    yield call(api.delete, `accounts/${id}`);

    toast.warn('Exclus√£o realizada com sucesso.');
    yield put(accountsDeleteSuccess(id));
  } catch (err) {

    const { error } = err.response.data

    if (error) {
      toast.error('N√£o √© poss√≠vel remover um usu√°rio com matr√≠cula vinculada!');
    } else {
      toast.error('Erro desconhecido ao rentar remover!');
    }

    yield put(accountsFailure());
  }
}

export default all([
  takeLatest('@account/ACCOUNT_SEARCH_REQUEST', searchAccounts),
  takeLatest('@account/ACCOUNT_SAVE_REQUEST', saveAccount),
  takeLatest('@account/ACCOUNT_DELETE_REQUEST', deleteAccount),
]);
