export function membershipsSearchRequest(data) {
  return {
    type: '@membership/MEMBERSHIP_SEARCH_REQUEST',
    payload: { data },
  };
}

export function membershipsSearchSuccess(data) {
  return {
    type: '@membership/MEMBERSHIP_SEARCH_SUCCESS',
    payload: data,
  };
}

export function membershipsSaveRequest(data) {
  return {
    type: '@membership/MEMBERSHIP_SAVE_REQUEST',
    payload: { data },
  };
}

export function membershipsSaveSuccess(data) {
  return {
    type: '@membership/MEMBERSHIP_SAVE_SUCCESS',
    payload: { data },
  };
}

export function membershipsDeleteRequest(id) {
  return {
    type: '@membership/MEMBERSHIP_DELETE_REQUEST',
    payload: { id },
  };
}

export function membershipsDeleteSuccess(id) {
  return {
    type: '@membership/MEMBERSHIP_DELETE_SUCCESS',
    id,
  };
}

export function membershipsFailure() {
  return {
    type: '@membership/MEMBERSHIP_FAILURE',
  };
}
