export function accountsSearchRequest(data) {
  return {
    type: '@account/ACCOUNT_SEARCH_REQUEST',
    payload: { data },
  };
}

export function accountsSearchSuccess(data) {
  return {
    type: '@account/ACCOUNT_SEARCH_SUCCESS',
    payload: { data },
  };
}

export function accountsSaveRequest(data) {
  return {
    type: '@account/ACCOUNT_SAVE_REQUEST',
    payload: { data },
  };
}

export function accountsSaveSuccess(data) {
  return {
    type: '@account/ACCOUNT_SAVE_SUCCESS',
    payload: { data },
  };
}

export function accountsDeleteRequest(id) {
  return {
    type: '@account/ACCOUNT_DELETE_REQUEST',
    payload: { id },
  };
}

export function accountsDeleteSuccess(id) {
  return {
    type: '@account/ACCOUNT_DELETE_SUCCESS',
    id,
  };
}

export function accountsFailure() {
  return {
    type: '@account/ACCOUNT_FAILURE',
  };
}
