import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { Link } from 'react-router-dom';
import { FaTrash, FaEdit, FaUserPlus, FaExclamationCircle } from 'react-icons/fa';

import { Container } from '../../../components/Grid';
import Title from '../../../components/Title';
import { Table, Thead, Th, Tbody, Tr, Td } from '../../../components/Table';
import InputSearch from '../../../components/InputSearch';
import Loading from '../../../components/Loading';
import { HeaderPage } from '../../../components/HeaderPage/styles';
import { Controls } from '../../../components/Controls/styles';

import colors from '../../../styles/colors';

import {
  accountsSearchRequest,
  accountsDeleteRequest,
} from '../../../store/modules/account/actions';

import Alert from '../../../util/alert';
import { Panel } from '../../../components/Panel/styles';
import NoResultFound from '../../../components/NoResultFound';
import PaginationInfo from '../../../components/Pagination/PaginationInfo';
import ButtonLikeLink from '../../../components/ButtonLikeLink';

export default function AccountList() {
  const [termSearch, setTermSearch] = useState('');
  const accounts = useSelector(state => state.account.accounts);
  const loading = useSelector(state => state.account.loading);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(accountsSearchRequest({ name: termSearch }));
  }, []); // eslint-disable-line

  function handleSearchMain(value) {
    setTermSearch(value);
    dispatch(accountsSearchRequest({ name: value }));
  }

  function handleDelete(id) {
    Alert.delete().then(result => {
      if (result.value) {
        dispatch(accountsDeleteRequest(id));
      }
    });
  }

  function formatCPF(cpf) {
    // Remove caracteres n√£o num√©ricos
    cpf = cpf.replace(/[^\d]/g, "");

    // Adiciona um ponto entre o terceiro e o quarto d√≠gitos
    cpf = cpf.replace(/(\d{3})(\d)/, "$1.$2");

    // Adiciona um ponto entre o sexto e o s√©timo d√≠gitos
    cpf = cpf.replace(/(\d{3})(\d)/, "$1.$2");

    // Adiciona um h√≠fen entre o nono e o d√©cimo d√≠gitos
    cpf = cpf.replace(/(\d{3})(\d{1,2})$/, "$1-$2");

    return cpf;
  }

  function formatHeight(cm) {
    if (typeof cm !== 'number' || isNaN(cm) || cm <= 0) {
      return 'Invalid input'; // ou voc√™ pode lan√ßar um erro
    }

    // Converte cent√≠metros para metros e formata a string
    var meters = cm / 100;
    return meters.toFixed(2); // Formata o n√∫mero para ter 2 casas decimais e adiciona 'm' no final
  }

  return (
    <Container>
      <HeaderPage>

        <Title>
          <FaUserPlus size={24} color="#121224" />
          Clientes
        </Title>
        <Controls>
          <InputSearch
            handleSearch={handleSearchMain}
            placeholder="Buscar por nome"
          />
        </Controls>
      </HeaderPage>

      {loading ? (
        <Loading>Carregando...</Loading>
      ) : (
        <Panel>
          {accounts.total === 0 ? (
            <NoResultFound />
          ) : (
            <>
              <Table>
                <Thead>
                  <Tr>
                    <Th style={{ width: "10%" }}>Id</Th>
                    <Th>Nome</Th>
                    <Th>E-mail</Th>
                    <Th>CPF</Th>
                    <Th align="center">Peso</Th>
                    <Th align="center">Altura</Th>
                    <Th colSpan="2" />
                  </Tr>
                </Thead>
                <Tbody>
                  {accounts.data.map(account => (
                    <Tr key={String(account.id)}>
                      <Td>{account.id}</Td>
                      <Td>{account.name}</Td>
                      <Td>{account.email}</Td>
                      <Td>{formatCPF(account.cpf)}</Td>
                      <Td align="center">{account.weight ? account.weight : <FaExclamationCircle size={16} color="#666666 " title="empty weight" />}</Td>
                      <Td align="center">{account.height ? formatHeight(account.height) : <FaExclamationCircle size={16} color="#666666 " title="empty height" />}</Td>
                      <Td align="right">
                        <Link
                          style={{ color: colors.edit }}
                          to={`/accounts/${account.id}/edit`}
                        >
                          <FaEdit size={16} color="#121224" title="editar usu√°rio" />
                        </Link>

                        <ButtonLikeLink
                          style={{ color: colors.delete, marginLeft: '10px' }}
                          onClick={() => handleDelete(account.id)}
                        >
                          <FaTrash size={16} color="#ce376e" title="remover usu√°rio" />
                        </ButtonLikeLink>
                      </Td>
                    </Tr>
                  ))}
                </Tbody>
              </Table>
              <br />
              <PaginationInfo
                total={accounts.total}
              />
            </>
          )}
        </Panel>
      )}
    </Container>
  );
}
