import React, { useEffect, useState } from 'react';
import { Form } from '@rocketseat/unform';
import { useDispatch } from 'react-redux';
import { useParams } from 'react-router-dom';
import { MdArrowBack } from 'react-icons/md';
import { MdDone } from 'react-icons/md';
import * as Yup from 'yup';

import { Container, Row, Column } from '../../../components/Grid';
import Title from '../../../components/Title';
import { HeaderPage } from '../../../components/HeaderPage/styles';
import { Controls } from '../../../components/Controls/styles';

import Input from '../../../components/Input';
import ButtonLink from '../../../components/ButtonLink';
import Button from '../../../components/Button';

import colors from '~/styles/colors';
import { Panel } from '../../../components/Panel/styles';
import Label from '../../../components/Label';
import { FormGroup } from '../../../components/FormGroup/styles';

import { FaUserPlus } from 'react-icons/fa';

import { accountsSaveRequest } from '../../../store/modules/account/actions';
import api from '../../../services/api';


// Fun√ß√£o para validar CPF
function validateCPF(cpf) {
  cpf = cpf.replace(/[^\d]+/g, '');
  if (cpf === '') return false;
  if (cpf.length !== 11 ||
    cpf === "00000000000" ||
    cpf === "11111111111" ||
    cpf === "22222222222" ||
    cpf === "33333333333" ||
    cpf === "44444444444" ||
    cpf === "55555555555" ||
    cpf === "66666666666" ||
    cpf === "77777777777" ||
    cpf === "88888888888" ||
    cpf === "99999999999")
    return false;
  let add = 0;
  for (let i = 0; i < 9; i++)
    add += parseInt(cpf.charAt(i)) * (10 - i);
  let rev = 11 - (add % 11);
  if (rev === 10 || rev === 11)
    rev = 0;
  if (rev !== parseInt(cpf.charAt(9)))
    return false;
  add = 0;
  for (let i = 0; i < 10; i++)
    add += parseInt(cpf.charAt(i)) * (11 - i);
  rev = 11 - (add % 11);
  if (rev === 10 || rev === 11)
    rev = 0;
  if (rev !== parseInt(cpf.charAt(10)))
    return false;
  return true;
}

// Extendendo o Yup para validar CPF
Yup.addMethod(Yup.string, 'cpf', function (message) {
  return this.test('cpf', message || 'CPF inv√°lido', value => validateCPF(value));
});


const schema = Yup.object().shape({
  name: Yup.string().required('Nome √© obrigat√≥rio'),
  email: Yup.string()
    .email('Insira um email v√°lido')
    .required('O email √© obrigat√≥rio'),
  cpf: Yup.string()
    .cpf('CPF inv√°lido')
    .required('O CPF √© obrigat√≥rio')
});

export default function AccountForm() {
  const { id } = useParams();
  const [account, setAccount] = useState({});
  const dispath = useDispatch();

  function handleSubmit(data) {
    dispath(accountsSaveRequest({ ...data, id }));
  }

  useEffect(() => {
    if (id) {
      async function loadAccount() {
        const res = await api.get(`accounts/${id}`);
        setAccount(res.data);
      }

      loadAccount();
    }
  }, [id]);

  return (
    <Container>
      <HeaderPage>
        <Title>
          <FaUserPlus size={24} color="#121224" />
          {id > 0 ? 'Editar' : 'Cadastrar'}
        </Title>
        <Controls>
          <ButtonLink to="/accounts" color={colors.second}>
            <MdArrowBack size={24} color="#121224" />
            <span>Voltar</span>
          </ButtonLink>
          <Button
            type="submit"
            label={id > 0 ? 'Atualizar' : 'Cadastrar'}
            icon={<MdDone size={24} color="#121224" />}
            form="formAccount"
          ></Button>
        </Controls>
      </HeaderPage>

      <Panel>
        <Form
          id="formAccount"
          initialData={account}
          schema={schema}
          onSubmit={handleSubmit}
        >
          <Row>
            <Column mobile="12" desktop="12">
              <FormGroup>
                <Input name="id" type="hidden" />
                <Label>Nome completo</Label>
                <Input name="name" placeholder="Digite seu nome completo" />
              </FormGroup>
            </Column>
          </Row>

          <Row>
            <Column mobile="12" desktop="8">
              <FormGroup>
                <Label>E-mail</Label>
                <Input
                  name="email"
                  type="email"
                  placeholder="Digite seu endere√ßo de email"
                />
              </FormGroup>
            </Column>
            <Column mobile="12" desktop="4">
              <FormGroup>
                <Label>CPF</Label>
                <Input type="text" name="cpf" placeholder="Seu CPF" />
              </FormGroup>
            </Column>
          </Row>
        </Form>
      </Panel>
    </Container>
  );
}
