import { takeLatest, call, put, all } from 'redux-saga/effects';

import { toast } from 'react-toastify';

import api from '../../../services/api';
import history from '../../../services/history';
import {
  membershipsFailure,
  membershipsSearchSuccess,
  membershipsSaveSuccess,
  membershipsDeleteSuccess,
} from './actions';

import { format, parseISO } from 'date-fns';
import pt from 'date-fns/locale/pt';

function* searchMemberships({ payload }) {
  try {
    const { term, page } = payload.data;

    const res = yield call(api.get, 'memberships', {
      params: {
        term,
        page,
      },
    });

    const data = res.data.data.map(item => ({
      ...item,
      startDate: format(
        parseISO(item.createdAt),
        "dd 'de' MMMM 'de' yyyy",
        {
          locale: pt,
        }
      )
    }));

    delete res.data.data;
    const pagination = res.data;

    yield put(membershipsSearchSuccess({ data, pagination }));
  } catch (error) {
    toast.error('Erro na consulta de matr√≠culas!');
    yield put(membershipsFailure());
  }
}

function* saveMembership({ payload }) {
  const { id } = payload.data;
  if (id) {
    yield updateMembership(payload.data);
  } else {
    yield addMembership(payload.data);
  }
}

function* addMembership(data) {
  try {
    const res = yield call(api.post, 'memberships', data);

    toast.warn('Matr√≠cula cadastrada com sucesso.');
    yield put(membershipsSaveSuccess(res.data));

    history.push('/memberships');
  } catch (err) {

    const { error } = err.response.data

    if (error === 'Membership already exists for the account') {
      toast.info('O usu√°rio j√° possui matr√≠cula.');
    } else {
      toast.error('Vixe! Erro desconhecido.');
    }

    yield put(membershipsFailure());
  }
}

function* updateMembership(data) {
  try {
    const res = yield call(
      api.put,
      `memberships/${data.id}`,
      data
    );

    toast.warn('Matr√≠cula atualizada com sucesso.');
    yield put(membershipsSaveSuccess(res.data));
  } catch (error) {
    toast.error('Erro ao atualizar dados da matr√≠cula!');
    yield put(membershipsFailure());
  }
}

function* deleteMembership({ payload }) {
  try {
    const { id } = payload;

    yield call(api.delete, `memberships/${id}`);

    toast.warn('Matr√≠cula removida com sucesso.');
    yield put(membershipsDeleteSuccess(id));
  } catch (err) {
    toast.error('Erro ao remover matr√≠cula!');
    yield put(membershipsFailure());
  }
}

export default all([
  takeLatest('@membership/MEMBERSHIP_SEARCH_REQUEST', searchMemberships),
  takeLatest('@membership/MEMBERSHIP_SAVE_REQUEST', saveMembership),
  takeLatest('@membership/MEMBERSHIP_DELETE_REQUEST', deleteMembership),
]);
