import produce from 'immer';

const INITIAL_STATE = {
  accounts: {
    page: 1,
    perPage: 5,
    total: 0,
    totalPage: 0,
    data: [],
  },
  account: {},
  loading: false,
};

export default function account(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      case '@account/ACCOUNT_SEARCH_REQUEST': {
        draft.loading = true;
        break;
      }

      case '@account/ACCOUNT_SEARCH_SUCCESS': {
        draft.accounts = action.payload.data;
        draft.loading = false;
        break;
      }

      case '@account/ACCOUNT_SAVE_REQUEST': {
        draft.loading = true;
        break;
      }

      case '@account/ACCOUNT_SAVE_SUCCESS': {
        draft.account = action.payload.data;
        draft.loading = false;
        break;
      }

      case '@account/ACCOUNT_DELETE_REQUEST': {
        draft.loading = true;
        break;
      }

      case '@account/ACCOUNT_DELETE_SUCCESS': {
        draft.loading = false;
        draft.accounts.data = state.accounts.data.filter(
          el => el.id !== action.id
        );
        break;
      }

      case '@account/ACCOUNT_FAILURE': {
        draft.loading = false;
        break;
      }

      default:
        return state;
    }
  });
}
