import React from 'react';
import { Switch } from 'react-router-dom';
import Route from './Route';

import Landing from '../pages/Landing';
import Welcome from '../pages/Welcome';

import Login from '../pages/Login';

import AccountList from '~/pages/Account/List';
import AccountForm from '~/pages/Account/Form';

import MembershipList from '~/pages/Membership/List';
import MembershipForm from '~/pages/Membership/Form';



export default function Routes() {
  return (
    <Switch>
      <Route path="/" component={Landing} exact />
      <Route path="/welcome" component={Welcome} exact />
      <Route path="/login" component={Login} exact />
      <Route path="/accounts" component={AccountList} isPrivate exact />
      <Route path="/accounts/new" component={AccountForm} isPrivate />
      <Route path="/accounts/:id/edit" component={AccountForm} isPrivate />

      <Route path="/memberships" component={MembershipList} isPrivate exact />
      <Route path="/memberships/new" component={MembershipForm} isPrivate />
    </Switch>
  );
}
