import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { Form } from '@rocketseat/unform';
import * as Yup from 'yup';

import { Container, Row, FormButton } from './styles';

import logo from '../../assets/logo-admin.svg';
import spinner from "../../assets/spinner.svg"

import { loginRequest } from '~/store/modules/auth/actions';
import Input from '~/components/Input';

const schema = Yup.object().shape({
  email: Yup.string()
    .email('Oops! O email informado √© inv√°lido')
    .required('Os campos email e senha s√£o obrigat√≥rios.'),
  password: Yup.string().required('Os campos email e senha s√£o obrigat√≥rios.')
});


export default function Login() {
  const [notice, setNotice] = useState();
  const [loading, setLoading] = useState(false);

  const dispath = useDispatch();

  async function handleSubmit({ email, password }) {
    setLoading(true)
    setNotice('');
    try {
      await schema.validate({ email, password });
      setTimeout(function(){
        dispath(loginRequest(email, password));
        setLoading(false)
     }, 1000)
    } catch (error) {
      setTimeout(function(){
         setNotice(error.message)
         setLoading(false)
      }, 300)
    }
  }

  return (
    <Container>
      <img src={logo} alt="Smartbit" />
      <Form onSubmit={handleSubmit}>

        <Row>
          <Input name="email" type="text" placeholder="Seu email" />
        </Row>
        <Row>
          <Input name="password" type="password" placeholder="Sua Senha" />
        </Row>
        <FormButton type="submit">
          {loading ? <img src={spinner} alt="spinner" /> : 'Entrar'}
        </FormButton>

        {notice &&
          <div className="notice">{notice}</div>
        }
      </Form>
    </Container>
  );
}
