import produce from 'immer';

const INITIAL_STATE = {
  memberships: [],
  pagination: {
    page: 1,
    perPage: 10,
    total: 0,
    totalPage: 0,
  },
  membership: {},
  loading: false,
};

export default function membership(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      case '@membership/MEMBERSHIP_SEARCH_REQUEST': {
        draft.loading = true;
        break;
      }

      case '@membership/MEMBERSHIP_SEARCH_SUCCESS': {
        draft.memberships = action.payload.data;
        draft.pagination = action.payload.pagination;
        draft.loading = false;
        break;
      }

      case '@membership/MEMBERSHIP_SAVE_REQUEST': {
        draft.loading = true;
        break;
      }

      case '@membership/MEMBERSHIP_SAVE_SUCCESS': {
        draft.membership = action.payload.data;
        draft.loading = false;
        break;
      }

      case '@membership/MEMBERSHIP_DELETE_REQUEST': {
        draft.loading = true;
        break;
      }

      case '@membership/MEMBERSHIP_DELETE_SUCCESS': {
        draft.loading = false;
        draft.memberships = state.memberships.filter(el => el.id !== action.id);
        break;
      }

      case '@membership/MEMBERSHIP_FAILURE': {
        draft.loading = false;
        break;
      }

      default:
        return state;
    }
  });
}
