import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import { FaTrash, FaPlus, FaUserCheck } from 'react-icons/fa';

import colors from '~/styles/colors';
import { Container } from '../../../components/Grid';
import { HeaderPage } from '../../../components/HeaderPage/styles';
import { Controls } from '../../../components/Controls/styles';
import Title from '../../../components/Title';
import { Table, Thead, Th, Tbody, Tr, Td } from '../../../components/Table';
import Loading from '../../../components/Loading';

import {
  membershipsSearchRequest,
  membershipsDeleteRequest,
} from '../../../store/modules/membership/actions';

import ButtonLink from '../../../components/ButtonLink';
import Alert from '../../../util/alert';
import { Panel } from '../../../components/Panel/styles';
import NoResultFound from '../../../components/NoResultFound';
import PaginationInfo from '../../../components/Pagination/PaginationInfo';
import ButtonLikeLink from '../../../components/ButtonLikeLink';
import InputSearch from '../../../components/InputSearch';

import { formatCurrencyBR } from '~/util';

export default function MembershipList() {
  const [termSearch, setTermSearch] = useState('');
  const memberships = useSelector(state => state.membership.memberships);
  const pagination = useSelector(state => state.membership.pagination);
  const loading = useSelector(state => state.membership.loading);
  const dispatch = useDispatch();

  function loadMemberships(term = termSearch) {
    dispatch(membershipsSearchRequest({ term }));
  }

  useEffect(() => {
    loadMemberships(termSearch);
  }, []); // eslint-disable-line

  function handleSearchMain(value) {
    setTermSearch(value);
    dispatch(membershipsSearchRequest({ term: value }));
  }

  function handleDelete(id) {
    Alert.delete().then(result => {
      if (result.value) {
        dispatch(membershipsDeleteRequest(id));
      }
    });
  }

  return (
    <Container>
      <HeaderPage>
        <Title>
          <FaUserCheck size={24} color="#121214" />
          Matr√≠culas
        </Title>
        <Controls>
          <ButtonLink to="/memberships/new">
            <FaPlus size={16} color="#121214" title="Matricular usu√°rio" />
            <span>Cadastrar</span>
          </ButtonLink>

          <InputSearch
            handleSearch={handleSearchMain}
            placeholder="Busque por nome"
          />
        </Controls>
      </HeaderPage>

      {loading ? (
        <Loading>Carregando...</Loading>
      ) : (
        <Panel>
          {pagination.total === 0 ? (
            <NoResultFound />
          ) : (
            <>
              <Table>
                <Thead>
                  <Tr>
                    <Th align="left">Id</Th>
                    <Th>Conta</Th>
                    <Th align="left">Plano</Th>
                    <Th align="left">Data de cadastro</Th>
                    <Th align="left">Cart√£o de cr√©dito</Th>
                    <Th align="left">Valor mensal</Th>
                    <Th />
                  </Tr>
                </Thead>
                <Tbody>
                  {memberships.map(membership => (
                    <Tr key={String(membership.id)}>
                      <Td width="10%">{membership.id}</Td>
                      <Td width="20%">{membership.account.name}</Td>
                      <Td align="left">
                        {membership.plan ? membership.plan.title : ''}
                      </Td>
                      <Td align="left">{membership.startDate}</Td>
                      <Td align="left">****.****.****.{membership.credit_card}</Td>
                      <Td align="left">{formatCurrencyBR(membership.price)}</Td>
                      <Td align="right">
                        <ButtonLikeLink style={{ color: colors.delete, marginLeft: '10px' }} onClick={() => handleDelete(membership.id)}>
                          <FaTrash size={16} color="#ce376e" title="remover matr√≠cula" />
                        </ButtonLikeLink>
                      </Td>
                    </Tr>
                  ))}
                </Tbody>
              </Table>
              <br />
              <PaginationInfo
                total={pagination.total}
              />
            </>
          )}
        </Panel>
      )}
    </Container>
  );
}
