import React, { useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import * as Yup from 'yup';
import { Form } from '@rocketseat/unform';
import { useParams } from 'react-router-dom';
import { MdArrowBack } from 'react-icons/md';
import { MdDone } from 'react-icons/md';

import colors from '~/styles/colors';
import { Container, Row, Column } from '../../../components/Grid';
import Title from '../../../components/Title';
import { HeaderPage } from '../../../components/HeaderPage/styles';
import { Controls } from '../../../components/Controls/styles';
import ButtonLink from '../../../components/ButtonLink';
import Button from '../../../components/Button';
import { Panel } from '../../../components/Panel/styles';
import { FormGroup } from '../../../components/FormGroup/styles';
import Select from '../../../components/Select';
import SelectAsync from '../../../components/SelectAsync';
import Input from '../../../components/Input';

import { FaUserCheck } from 'react-icons/fa'
import { AiFillApple, AiFillAndroid, AiOutlineCheck } from 'react-icons/ai'

import api from '../../../services/api';

import { membershipsSaveRequest } from '../../../store/modules/membership/actions';

import valid from 'card-validator';

import { FormContent, PlanTable } from './styles';

const schema = Yup.object().shape({
  select_account: Yup.number()
    .transform((value) => (isNaN(value) ? undefined : value)).nullable()
    .required('O usu√°rio √© obrigat√≥rio'),
  select_plan: Yup.number()
    .transform((value) => (isNaN(value) ? undefined : value)).nullable()
    .required('O plano √© obrigat√≥rio'),
  card_number: Yup.string()
    .test('test-number', // this is used internally by yup
      'Cart√£o de cr√©dito inv√°lido', //validation message
      value => valid.number(value).isValid) // return true false based on validation
    .required('O cart√£o de cr√©dito √© obrigat√≥rio'),
  card_holder: Yup.string()
    .required('O nome do titular √© obrigat√≥rio'),
  card_month: Yup.number()
    .transform((value) => (isNaN(value) || value === null || value === undefined) ? null : value)
    .typeError('O m√™s √© obrigat√≥rio')
    .min(1, 'M√™s inv√°lido')
    .max(12, 'M√™s inv√°lido'),
  card_year: Yup.number()
    .transform((value) => (isNaN(value) || value === null || value === undefined) ? null : value)
    .typeError('O ano √© obrigat√≥rio')
    .min(2023, 'Ano inv√°lido')
    .max(2099, 'Ano inv√°lido'),
  card_cvv: Yup.number()
    .transform((value) => (isNaN(value) || value === null || value === undefined) ? null : value)
    .typeError('O CVV √© obrigat√≥rio')
    .min(100, 'CVV inv√°lido')
    .max(999, 'CVV inv√°lido')
});

export default function MembershipForm() {
  const dispath = useDispatch();
  const { id } = useParams();

  const [plans, setPlans] = useState([]);
  const [accounts, setAccounts] = useState([]);
  const [accountSelected, setAccountSelected] = useState(null);
  const [planSelected, setPlanSelected] = useState(null);


  async function loadPlans() {
    const res = await api.get('plans');
    setPlans(res.data.data);
  }

  async function loadAccounts(value) {
    const res = await api.get('accounts', {
      params: {
        name: value,
        page: 1,
        perPage: 10,
      },
    });

    const data = res.data.data;

    setAccounts(data.map(x => ({ ...x, value: x.id })));

    return new Promise(resolve => {
      resolve(data);
    });
  }

  function handleSubmit(values) {
    const data = {
      account_id: values.select_account,
      plan_id: values.select_plan,
      credit_card: values.card_number.substr(12, 16),
      id,
    };
    dispath(membershipsSaveRequest(data));
  }

  useEffect(() => {
    loadPlans();
    loadAccounts();
  }, []);

  return (
    <Container>
      <HeaderPage>
        <Title>
          <FaUserCheck size={24} color="#121214" />
          Nova matr√≠cula
        </Title>
        <Controls>
          <ButtonLink to="/memberships" color={colors.second}>
            <MdArrowBack size={24} color="#121224" />
            <span>Voltar</span>
          </ButtonLink>
          <Button
            type="submit"
            label="Cadastrar"
            icon={<MdDone size={24} color="#121224" />}
            form="formMembership"
          ></Button>
        </Controls>
      </HeaderPage>

      <Panel>
        <FormContent>
          <Form
            id="formMembership"
            schema={schema}
            onSubmit={handleSubmit}
          >

            <Row>
              <Column mobile="12" desktop="5" className="account">

                <Row>
                  <h3>Dados de plano</h3>
                  <Column mobile="12" desktop="12" className="account-name">
                    <FormGroup>
                      <SelectAsync
                        placeholder="Digite o nome completo..."
                        name="select_account"
                        options={accounts}
                        value={accountSelected}
                        onChange={e => setAccountSelected(e)}
                        asyncFunc={loadAccounts}
                      />
                    </FormGroup>
                  </Column>

                  <Column mobile="12" desktop="12">
                    <FormGroup>
                      <Select
                        placeholder="Selecione o plano..."
                        name="select_plan"
                        options={plans}
                        value={planSelected}
                        getOptionLabel={option => `Plano ${option.title}`}
                        onChange={e => setPlanSelected(e)}
                      />
                    </FormGroup>
                  </Column>

                </Row>
                
                <Row className="cc_title">
                  <h3>Cart√£o de cr√©dito</h3>
                </Row>

                <Row className="payment">
                  <h3>Pagamento recorrente</h3>
                  <Row >

                    <Column mobile="12" desktop="12">
                      <FormGroup>
                        <Input name="card_number" maxLength="16" placeholder="N√∫mero do cart√£o de cr√©dito" />
                      </FormGroup>
                    </Column>
                  </Row>
                  <Row>
                    <Column mobile="12" desktop="12">
                      <FormGroup>
                        <Input name="card_holder" placeholder="Digite o nome impresso no cart√£o" />
                      </FormGroup>
                    </Column>
                  </Row>
                  <Row>
                    <Column mobile="12" desktop="4">
                      <FormGroup>
                        <Input name="card_month" type="number" placeholder="MM" />
                      </FormGroup>
                    </Column>
                    <Column mobile="12" desktop="4">
                      <FormGroup>
                        <Input name="card_year" type="number" placeholder="AAAA" />
                      </FormGroup>
                    </Column>
                    <Column mobile="12" desktop="4">
                      <FormGroup>
                        <Input name="card_cvv" type="number" placeholder="CVV" />
                      </FormGroup>
                    </Column>

                  </Row>

                </Row>
              </Column>

              <Column mobile="12" desktop="7" className="plans">
                <PlanTable>
                  <div className="plan-table">
                    {plans.map(plan => (
                      <div className={plan.duration === 1 ? 'ptable-item featured-item' : 'ptable-item'} key={String(plan.id)}>
                        <div className="ptable-single">
                          <div className="ptable-header">
                            <div className="ptable-title">
                              <h2>{plan.title}</h2>
                            </div>
                            <div className="ptable-price">
                              <h2><small>R$</small>{plan.price.split('.')[0]}<span>, {plan.price.split('.')[1]}</span></h2>
                            </div>
                          </div>
                          <div className="ptable-body">
                            <div className="ptable-description">
                              <ul>
                                <li><AiOutlineCheck size={18} color="#121214" /> {plan.duration === 1 ? 'Cancele quando quiser' : `Perman√™ncia m√≠nima de ${plan.duration} meses`}</li>
                                <li><AiOutlineCheck size={18} color="#121214" /> Sem taxas de matr√≠cula ou manuten√ß√£o</li>
                                <li><AiOutlineCheck size={18} color="#121214" /> √Årea de muscula√ß√£o e aer√≥bicos</li>
                                <li><AiOutlineCheck size={18} color="#121214" /> Sem restri√ß√£o de hor√°rios</li>
                                <li><AiFillAndroid size={22} color="#78C257" /> Aplicativo para Android</li>
                                <li><AiFillApple size={22} color="#555555" /> Aplicativo para iPhone (em breve)</li>
                              </ul>
                            </div>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                </PlanTable>
              </Column>
            </Row>
          </Form>
        </FormContent>
      </Panel>
    </Container>
  );
}